.goal_input {
    margin: 0.5rem 0;
  }

  .goal_input input {
    display: block;
    width: 100%;
    border: 1px solid #ccc;
    font: inherit;
    line-height: 1.5rem;
    padding: 0 0.25rem;
  }

  .goal_input input:focus {
    outline: none;
    background: #fad0ec;
    border-color: #8b005d;
  }
